<template>
  <div>注:该页面还需后面再优化,先把数据摆上来
    <ul v-for="item in data"
      :key="item.id">
      <li>{{item}}
      </li>

    </ul>

  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'WordList',
  data() {
    return {
      data: [],
    }
  },
  mounted() {
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      alert('请先登录')
      this.$router.push('/login')
    }
    axios({
      method: 'post',
      url: 'http://121.40.113.226:7778/word/todayWords',
      data: {
        uid: this.$store.state.uid,
      },
    }).then((res) => {
      // console.log(res)
      this.data = res.data.data
      if(this.data.length==0){
        alert("今天还没有单词要背哦,先去添加单词吧,添加完一般第二天才可以获取,记得今天先背添加的单词哦")
      }
    })
  },
}
</script>
